import {
  defineNuxtPlugin,
  useRequestEvent,
  useServerHead
} from "#imports";
export default defineNuxtPlugin({
  setup() {
    const event = useRequestEvent();
    const ctx = event.context.robots;
    if (!ctx)
      return;
    useServerHead({
      meta: [
        {
          "name": "robots",
          "content": () => ctx.rule || "",
          "data-hint": import.meta.dev && !ctx.indexable ? "disabled in development" : void 0
        }
      ]
    });
  }
});
